@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300&display=swap);
body {
  margin: 0;
  font-family: 'Titillium Web', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navTitle{
    color: white;
    font-weight: 800;
}

.Navbar{
    justify-content: space-evenly;
    border-bottom: 2px solid rgb(167, 167, 167);
    background-color: rgb(32, 32, 32);
    font-weight: 700;
    padding-top: 20px;
    align-content: center;
}

.navbarLink{
    color: black;
    transition: 0.5s;
    color: rgb(141, 141, 141);
    font-size: x-large;
}

.navbarLink:hover{
    text-decoration: none;
    color: rgb(194, 194, 194);
    transition: 0.3s;
}

.submitButton{
    padding: 0;
}

.signupText{
    color: white;
    font-size: x-large;
    margin-right: 5px;
    font-weight: 300;
}
.Footer{
    border-top: 2px solid grey;
    width: 100%;
    justify-content: center;
}

.footerText{
    padding-top: 5px;
}
.carousel-item{
    max-height: 70vh;
}

.carousel-item img{
    width: 100%
}

.homeCarousel{
    border-bottom: 2px solid #414141d8;
}

.newsMedia{
    margin-bottom: 35px;
    padding: 15px;

    transition: all 0.3s ease-in;
}

.newsMedia:hover{
    background-color: rgb(100, 100, 100);
    color: white;
}

.newsPicture{
    border: 2px solid black;
    box-shadow: 7px 6px 25px -9px rgba(0, 0, 0, 0.856);
}

.newsHeader{
    text-align: center;
    font-weight: 800;
    padding-top: 10px;
}

.newsCol{
    padding: 0;
    background: rgb(204,204,204);
    background: linear-gradient(90deg, rgba(201, 201, 201, 0.534) 0%, rgba(255,255,255,1) 100%);
}

.newsTitle{
    font-size: x-large;
    font-weight: 900;
    border-bottom: 2px solid grey;
    color: black;
}

.newsTitle:hover {
    color: white;
}

.newsText{
    font-size: larger;
    font-weight: 500;
}
.sideGame{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-content: center;
    margin: 0;
    margin-bottom: 20px;
    padding: 15px 7px;
    background-color: rgb(218, 218, 218);
    border-radius: 15%;
    box-shadow: 7px 6px 10px -9px rgba(0,0,0,0.75);
    border: 2px solid rgb(116, 116, 116);
    transition: all 0.2s ease-in;
}

.liveGame {
    color: black;
}

.liveGame:hover {
    color: black;
}

.gameClock{
    text-align: center;
    padding-left: 25px;
    padding-right: 25px;
}

.sideGame:hover {
    background-color: rgb(255, 255, 255);
}

.sideGame p{
    margin-bottom: 0;

}

.teamLogo{
    width: 40px;
    height: 40px;
}

.teamOne{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-basis: 45%;
    font-size: large;
    font-weight: 600;
}

.teamOne img{
    margin-right: auto;
}

.teamTwo{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-basis: 45%;
    font-size: large;
    font-weight: 600;
}

.teamTwo img{
    margin-left: auto;
}

.scoreHead{
    text-align: center;
    font-weight: 500;
    padding-top: 10px;
}
.scoreBox{
    padding: 0;
    padding-right: 10px;
    padding-left: 10px;
    border-left: 3px solid rgb(107, 107, 107);
}

.teamsContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 15px;
}
.teamCard{
    border: 2px solid #000000;
    width: 250px;
    height: 200px;
    margin: 5px;
    text-align: center;
    color: white;
    font-weight: 700;
    border-radius: 15%;
    box-shadow: 4px 2px 19px -5px rgba(0,0,0,0.75);
}

